.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

img {
  max-width: 100%;
}

.form-control-lg,
.btn-lg,
.btn-group-lg > .btn {
  font-size: 0.75rem;
}
@media (min-width: 600px) {
  .form-control-lg,
.btn-lg,
.btn-group-lg > .btn {
    font-size: 1rem;
  }
}
@media (min-width: 782px) {
  .form-control-lg,
.btn-lg,
.btn-group-lg > .btn {
    font-size: 1rem;
  }
}

.form-check-input {
  margin-top: 0.2rem;
}

a {
  transition: ease 0.3s;
}
a:hover {
  text-decoration: none;
}

/* barlow-semi-condensed-300 - latin */
