  font-style: normal;
  font-weight: 300;
  src: local("Barlow Semi Condensed Light"), local("BarlowSemiCondensed-Light"), url("../fonts/barlow-condensed/barlow-semi-condensed-v2-latin-300.woff2") format("woff2"), url("../fonts/barlow-condensed/barlow-semi-condensed-v2-latin-300.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
